import React, { Component } from 'react';
import folio1 from '../img/folio1.jpg';
import folio2 from '../img/folio2.jpg';
import folio3 from '../img/folio3.jpg';
import folio4 from '../img/folio4.jpg';
import folio5 from '../img/folio5.jpg';
import folio6 from '../img/folio6.jpg';
import folio8 from '../img/folio8.jpg';
import folio9 from '../img/folio9.jpg';
import folio7 from '../img/folio7.jpg';


class Portfolio extends Component {
    render() {
        return (
            <div className="condivp">
                <div className="row">
                    <div className="column">
                        <img src={folio1} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio2} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio3} className="portfolio" alt="Portfolio "></img>
                    </div>
                </div>
                <div className="row">
                    <div className="column">
                        <img src={folio4} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio5} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio6} className="portfolio" alt="Portfolio "></img>
                    </div>
                </div>
                <div className="row">
                    <div className="column">
                        <img src={folio7} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio8} className="portfolio" alt="Portfolio "></img>
                    </div>
                    <div className="column">
                        <img src={folio9} className="portfolio" alt="Portfolio "></img>
                    </div>
                </div>
            </div>
        )
    }
}

export default Portfolio;