import React, { Component } from 'react';
import Widecard from '../components/Widecard';
class Education extends Component {
    render () {
        return (
            <div className="condiv">
                <h1 className="subtopic">My Experience</h1>
                <Widecard title="Graphic Designer" where="NC Department of Public Instruction" from="2018" to="Present"/>
                <Widecard title="Art Director" where="Greenville Magazine | Her Magazine" from="2017" to="2018"/>
                <Widecard title="Art Director" where="Washington Newsmedia" from="2014" to="2017"/>
                <h1 className="subtopic">My Education</h1>
                <Widecard title="BFA Graphic Design | Minor Art History" where="East Carolina University" from="2012" to="2014"/>
                <Widecard title="BS Biological Business | Minor Economics" where="North Carolina State University" from="1996" to="2001"/>
            </div>
        )
    }
}

export default Education;