import React, { Component } from 'react'
import iconID from '../img/iconID.png';
import iconAI from '../img/iconAI.png';
import iconPS from '../img/iconPS.png';
import iconDW from '../img/iconDW.png';
import iconH5 from '../img/iconH5.png';
import iconC3 from '../img/iconC3.png';
import iconJS from '../img/iconJS.png';
import iconRX from '../img/iconRX.png';
import iconCA from '../img/iconCA.png';
import iconOF from '../img/iconOF.png';
import iconAP from '../img/iconAP.png';
import iconWN from '../img/iconWN.png';


class Skills extends Component {

    constructor(props) {
        super(props);
        this.state={
            'myskills':['InDesign','Illustrator','Photoshop','Dreamweaver','HTML','CSS','JS','REACT JS','Photography','MS Office','Mac and Windows']
        };
    }

    render() {
        return (
            <div className="condiv skills">
                <h1 className="subtopic">My Skills</h1>
                <ul className="columns">
                    <div className="left">
                    <li><img src={iconID} className="icon" alt="InDesign logo"></img>InDesign</li>
                    <li><img src={iconAI} className="icon" alt="Illustrator logo"></img>Illustrator</li>
                    <li><img src={iconPS} className="icon" alt="Photoshop logo"></img>Photoshop</li>
                    <li><img src={iconDW} className="icon" alt="Dreamweaver logo"></img>Dreamweaver</li>
                    <li><img src={iconH5} className="icon" alt="HTML5 logo"></img>HTML5</li>
                    <li><img src={iconC3} className="icon" alt="CSS3 logo"></img>CSS3</li>
                    </div>
                    <div className="right">
                    <li><img src={iconJS} className="icon" alt="Javascript logo"></img>Javascript</li>
                    <li><img src={iconRX} className="icon" alt="React logo"></img>React JS</li>
                    <li><img src={iconCA} className="icon" alt="Photography icon"></img>Photography</li>
                    <li><img src={iconOF} className="icon" alt="Office 365 logo"></img>MS Office</li>
                    <li><img src={iconAP} className="icon" alt="Apple logo"></img>Mac OS</li>
                    <li><img src={iconWN} className="icon" alt="Windows logo"></img>Windows</li>
                    </div>
                </ul>
            </div>
        )
    }
}

export default Skills