import React, { Component } from 'react';
class About extends Component {
    render () {
        return (
            <div className="condiv">
                <h1 className="subtopic">About Me</h1>
                <h3>Hello!</h3>
                <h2>I'm Jason Scott</h2>
                <h3>Graphic and Web Designer</h3>
                <br></br>
                <p>I'm a graphic designer based in the Raleigh, North Carolina area with experience in art direction, print design, branding, web design, journalism and photography. 
                    I served eight years in the U.S. Army as an electronics technician and a public affairs specialist. 
                    I hold degrees in Biological Business Management from N.C. State and Graphic Design from East Carolina University.</p>
            </div>
        )
    }
}

export default About;